import React from 'react';
import { Navbar, Nav, NavItem, Image} from 'react-bootstrap';

class Navigation extends React.Component {
  render() {
    return (
      <div>
        <Navbar fixedTop collapseOnSelect>
          <Navbar.Header>
            <Navbar.Brand>
              <a href="/"><Image className="brand-image" src={require("../../assets/images/headshot.png")} rounded width={23} height={25}/></a>
            </Navbar.Brand>
            <Navbar.Toggle />
          </Navbar.Header>
          <Navbar.Collapse>
          <Nav>
            <NavItem eventKey={1} href="https://dlause.blogspot.com">Content</NavItem>
            <NavItem eventKey={2} href="/portfolio">Portfolio</NavItem>
            <NavItem eventKey={3} href="/shop">Store</NavItem>
            <NavItem eventKey={4} href="/contact">Contact</NavItem>
            </Nav>
          <Nav pullRight>
            <NavItem eventKey={7} href="https://linkedin.com/in/dereklause/"><i className="fa fa-linkedin"/></NavItem>
            <NavItem eventKey={9} href="https://github.com/dmlause"><i className="fa fa-github"/></NavItem>
            <NavItem eventKey={5} href="https://twitter.com/dmlause"><i className="fa fa-twitter"/></NavItem>
            <NavItem eventKey={6} href="https://facebook.com/dmlause"><i className="fa fa-facebook"/></NavItem>
            <NavItem eventKey={8} href="https://youtube.com/channel/UCNoAX-XqNBMlP7CkEYgewVg"><i className="fa fa-youtube-play"/></NavItem>
          </Nav>
          </Navbar.Collapse>
        </Navbar>
      </div>
    );
  }
}

export default Navigation;



