import React from 'react';
import { Grid, Row, Col } from 'react-bootstrap';

class About extends React.Component {
  render() {
    return (
      <Grid fluid>
        <Row className="about-row">
          {/* Render this column for screens that aren't phones */}
          <Col xs={8} xsHidden xsOffset={2}>
            <h2 className="welcome-text">Hi, I'm Derek Lause.</h2>
            <div className="about-container">
              <img title="Derek Lause" className="about-image" alt="Headshot" src={require("../../assets/images/headshot.png")} />
              <p className="about-text">
                I am a tech fanatic, software developer, and video game player.
                I love to be around friends and family while fulfilling my life by accomplishing goals and striving to improve.
                I enjoy fantastic views, biking, eSports, great food, and a couple beers on occasion.
                This website consists of written <a href='https://dlause.blogspot.com' className="about-link">content</a> of mine,
                a <a href='/portfolio' className="about-link">portfolio</a> of my current work, and a <a href='/shop' className="about-link">store</a>.
                If one would wish to contact me, please visit the <a href='/contact' className="about-link">contact page</a>.
                Follow me on <a href="https://linkedin.com/in/dereklause/" className="about-link">LinkedIn</a>, <a href="https://github.com/dmlause" className="about-link">GitHub</a>, <a href="https://twitter.com/dmlause" className="about-link">Twitter</a>, <a href="https://facebook.com/dmlause" className="about-link">Facebook</a>, and <a href="https://youtube.com/channel/UCNoAX-XqNBMlP7CkEYgewVg" className="about-link">YouTube</a>! 
              </p>
            </div>
          </Col>
          {/* Render this column for screens that are phones */}
          <Col xs={8} smHidden mdHidden lgHidden xsOffset={2}>
            <h2 className="welcome-text">Hi, I'm Derek Lause.</h2>
            <div className="about-container">
              <p className="about-text">
                I am a tech fanatic, software developer, constantly learning individual, and video game pgit layer.
                I love to be around friends and family while fulfilling my life by accomplishing goals and striving to improve.
                I enjoy fantastic views, biking, eSports, great food, and a couple beers on occasion.
                This website consists of written <a href='https://dlause.blogspot.com' className="about-link">content</a> of mine,
                a <a href='/portfolio' className="about-link">portfolio</a> of my current work, and a <a href='/shop' className="about-link">store</a>.
                If one would wish to contact me, please visit the <a href='/contact' className="about-link">contact page</a>.
                Follow me on <a href="https://linkedin.com/in/dereklause/" className="about-link">LinkedIn</a>, <a href="https://github.com/dmlause" className="about-link">GitHub</a>, <a href="https://twitter.com/dmlause" className="about-link">Twitter</a>, <a href="https://facebook.com/dmlause" className="about-link">Facebook</a>, and <a href="https://youtube.com/channel/UCNoAX-XqNBMlP7CkEYgewVg" className="about-link">YouTube</a>! 
              </p>
            </div>
            <img title="Derek Lause" className="about-image-m" alt="Headshot" src={require("../../assets/images/headshot.png")} />
          </Col>
        </Row>
      </Grid>
    );
  }
}

export default About;


